



























































/* 常に表示 */
.expand-transition {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  height: 300px;
  overflow: hidden;
}

/* .expand-enter は entering に対する開始状態を定義 */
/* .expand-leave は leaving に対する終了状態を定義 */
.expand-enter, .expand-leave {
  height: 0;
  opacity: 0;
}



